import React, { Suspense } from "react";

//import detect from "detect.js"
//import Logo from "../../../../Shared/Logo/Logo";
//import SignOut from "../../../../Shared/icons/SignOut/Sign-out";
import Menu from "../Menu/Menu";
import './Header.scss';
//import LineMenu from "../../../../Shared/icons/LineMenu/Line-menu";
//import Drawer from "@material-ui/core/Drawer";
const Logo = React.lazy( () => import('../../../../Shared/Logo/Logo') );
const LineMenu = React.lazy( () => import('../../../../Shared/icons/LineMenu/Line-menu') );
const CurrentUser = React.lazy( () => import('../CurrentUser/CurrentUser') );
const Drawer = React.lazy( () => import('@material-ui/core/Drawer') );
const SignOut = React.lazy( () => import('../../../../Shared/icons/SignOut/Sign-out') );

export default class Header extends React.Component {
    // state = {
    //     mobileMenuIsOpen: false,
    // };
    //
    // changeMobileMenuStatus = () => {
    //   this.setState( ({ mobileMenuIsOpen }) => {
    //       return {
    //           mobileMenuIsOpen: !mobileMenuIsOpen
    //       }
    //     });
    // };

    render() {
        const menuStatus = this.props.menuStatus;
        const menuChanger = this.props.menuChanger;
        const contentText = this.props.contentText;

        const toggleDrawer = (open) => event => {
            if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
                return;
            }
            menuChanger(open);
        };

        return (
            <header className="mainHeader">
                <div className="mainHeader__container">
                    <div className="mainHeader__logo">
                        <Suspense fallback=''>
                            <Logo className="mainHeader__logo-item" />
                        </Suspense>
                    </div>
                    <div className="mainHeader__navigation">
                        <div className="mainHeader__menu">
                                <Menu
                                    menuItems={contentText.menuItems}
                                />
                        </div>
                        <div className="mainHeader__current-user">
                            <Suspense fallback=''>
                                <CurrentUser />
                            </Suspense>
                            <div className="mainHeader__sign-out">
                                <Suspense fallback=''>
                                    <SignOut />
                                </Suspense>
                            </div>
                        </div>
                        <div className="mainHeader__menu-mobile-toggle">
                            <Suspense fallback=''>
                                <LineMenu onClick={toggleDrawer(true)}/>
                            </Suspense>
                            {/*<LineMenu onClick={() => this.changeMobileMenuStatus(true)} />*/}
                        </div>
                    </div>
                </div>
                {/*<div className={`mainHeader__menu-mobile-bg${this.state.mobileMenuIsOpen ? '' : ' disabled'}`} onClick={() => this.changeMobileMenuStatus(this.state.mobileMenuIsOpen)}/>*/}
                {/*<div className={`mainHeader__menu-mobile${this.state.mobileMenuIsOpen ? '' : ' disabled'}`}>*/}
                {/*    <div className="">*/}
                {/*        <CurrentUser />*/}
                {/*    </div>*/}
                {/*</div>*/}
                <Suspense fallback=''>
                    <Drawer open={menuStatus} onClose={toggleDrawer(false)}>
                        <div
                            className="mainHeader__sidebar-mobile"
                            role="presentation"
                            onClick={toggleDrawer(false)}
                            onKeyDown={toggleDrawer(false)}
                        >
                            <div className="mainHeader__sidebar-mobile-current-user">
                                <CurrentUser isMobile/>
                            </div>
                            <hr className="mainHeader__sidebar-mobile-hr"/>
                            <div className="mainHeader__sidebar-mobile-menu">
                                <Menu
                                    menuItems={contentText.menuItems}
                                    isMobile
                                />
                            </div>
                        </div>
                    </Drawer>
                </Suspense>
            </header>
        )
    }
}