import React from "react";

import './WelcomeBlock.scss';
import '../../../../Shared/Scss/typography.scss';
import '../../../../Shared/Scss/buttons.scss';
import Button from "@material-ui/core/Button";

import '../../../../Shared/images/bg/plain/2k.jpg';
import '../../../../Shared/images/bg/plain/desktop.jpg';
import '../../../../Shared/images/bg/plain/tablet.jpg';
import '../../../../Shared/images/bg/plain/mobile.jpg';

import '../../../../Shared/images/bg/retina/2k.jpg';
import '../../../../Shared/images/bg/retina/desktop.jpg';
import '../../../../Shared/images/bg/retina/tablet.jpg';
import '../../../../Shared/images/bg/retina/mobile.jpg';

import detect from "detect.js";

export default class WelcomeBlock extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            bannerContentText: '',
        };

        this.contentText = this.props.contentText;

        const userAgent = detect.parse(navigator.userAgent);
        this.isNeedToFix = /Windows|Android/i.test(userAgent.os.family);
        this.isFirefox = /Firefox/i.test(userAgent.browser.family);
        console.log(userAgent.os.family);
        console.log(userAgent.browser.family);
        const maxMobileSize = 755;
        this.plainText = this.contentText.plainText;
        this.shortText = this.contentText.shortText;

        this.correctTextChecker = () => {
            if (document.body.clientWidth > maxMobileSize) {
                this.setState({
                    bannerContentText: this.plainText,
                });
                return;
            }

            this.setState({
                bannerContentText: this.shortText,
            });
        };
    }

    componentDidMount() {
        this.correctTextChecker();
        window.addEventListener("resize", this.correctTextChecker);
    }

    componentWillReceiveProps(nextProps, nextContext) {
        this.contentText = nextProps.contentText;

        this.plainText = this.contentText.plainText;
        this.shortText = this.contentText.shortText;
        this.correctTextChecker();
    }

    render() {
        //console.log('banner ', this.contentText);
        console.log('windows ', this.isNeedToFix);
        console.log('firefox ', this.isFirefox);

        return (
            <section className="welcome-block">
               <div className="welcome-block__container">
                   <div className="welcome-block__content">
                       <h1 className={`heading1${this.isNeedToFix || this.isFirefox ? ' overpassFix' : ''}`}>{this.contentText.title}</h1>
                       <p className="welcome-block__content-text">{this.state.bannerContentText}</p>
                       <div className="welcome-block__content-button button">
                           <Button variant="contained" className="button-item button-item_primary">
                               {this.contentText.buttonSignUp}
                           </Button>
                       </div>
                   </div>
               </div>
            </section>
        )
    }
}